.wrapper {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  grid-template-columns: repeat(6, 1fr);
  align-content: start;
  min-width: 320px;
  max-width: 100vw;
  min-height: 100vh;
  margin: 60px auto;
  padding: 0 6.4vw;
  background-color: #FFFFFF;
  box-shadow: 0px 24px 80px rgba(0, 0, 0, 0.1); }
  @media (min-width: 630px) {
    .wrapper {
      padding: 0;
      max-width: 960px; } }
  @media (min-width: 860px) {
    .wrapper {
      grid-template-columns: repeat(12, 1fr);
      grid-auto-flow: row;
      max-width: 1120px;
      min-height: 500px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

html {
  height: 100%; }

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  margin: 0;
  padding: 0; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

h2 {
  font-size: 3vw;
  color: #fff;
  margin-bottom: 3vw; }

*,
*::before,
*::after {
  box-sizing: border-box; }

.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 141px 0;
  max-width: 810px; }

.game__data {
  margin-bottom: 30px; }

.game__form {
  display: flex;
  width: 50vw; }
  @media (min-width: 630px) {
    .game__form {
      width: 500px; } }
  @media (min-width: 860px) {
    .game__form {
      width: 810px; } }

.game__input {
  flex-grow: 1;
  padding: 2vw;
  font-size: 2vw;
  font-weight: 300;
  text-transform: uppercase;
  border: 1px solid #A0A0A0; }
  .game__input::placeholder {
    color: #000000; }
  @media (min-width: 860px) {
    .game__input {
      font-size: 30px;
      padding: 22px 27px 25px; } }

.game__content {
  width: 100%;
  margin-bottom: 123px; }

.game__item {
  display: flex;
  width: 180px;
  height: 220px;
  background-color: #DADADA;
  cursor: pointer;
  transition: 0.4s ease; }
  .game__item:hover {
    transform: scale(1.07);
    box-shadow: 1px 1px 12px #586a72; }

.game__img {
  object-fit: cover;
  flex: 1; }

.game__party {
  display: flex;
  flex-direction: column;
  align-items: center; }

.data__form {
  display: flex;
  width: 50vw; }
  @media (min-width: 860px) {
    .data__form {
      width: 810px; } }

.data__input {
  flex-grow: 1;
  padding: 2vw;
  font-size: 2vw;
  font-weight: 300;
  text-transform: uppercase;
  border: 1px solid #A0A0A0;
  color: #000000; }
  .data__input::placeholder {
    color: #DADADA; }
  @media (min-width: 860px) {
    .data__input {
      font-size: 30px;
      padding: 22px 27px 25px; } }

.content__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.content__item {
  position: relative;
  margin: 0 30px 30px 0;
  background-color: #A8D4E9; }
  .content__item:active {
    opacity: 0.5;
    transform: scale(1); }
  @media (min-width: 860px) {
    .content__item:nth-child(4n + 4) {
      margin: 0 0 30px 0; }
    .content__item:last-child {
      margin: 0 0 30px 0; } }

.content__close-button {
  position: absolute;
  top: 8px;
  right: 8px;
  width: 30px;
  height: 30px;
  padding: 0;
  background-color: rgba(255, 255, 255, 0.75);
  border: none;
  border-radius: 50%;
  cursor: pointer; }
  .content__close-button:hover {
    background-color: #FFFFFF; }
  .content__close-button:active {
    background-color: rgba(255, 255, 255, 0.75); }
  .content__close-button::before {
    content: ' ';
    position: absolute;
    top: 11px;
    right: 10px;
    width: 10px;
    height: 10px;
    background-image: url(../../img/cross.svg);
    background-repeat: no-repeat;
    background-position: center; }
  .content__close-button:hover::before {
    opacity: 0.7; }
  .content__close-button:active::before {
    opacity: 0.4; }

.party__caption {
  font-size: 30px;
  font-weight: 700;
  color: #000000;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 20px; }

.party__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px; }

.party__item {
  position: relative;
  display: flex;
  justify-content: center; }

.party__name {
  position: absolute;
  bottom: 30px;
  margin: 0;
  font-size: 30px;
  font-weight: 300;
  color: #FFFFFF;
  text-transform: uppercase; }

.party__img {
  z-index: 1; }

.error {
  width: 100%;
  height: 5vw;
  background-color: #ff4949;
  color: #fff;
  font-size: 2vw;
  top: 0;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center; }
